import PropTypes from 'prop-types'
import React from 'react';
import { Grid, GridColumn, GridContainer } from '../../components/Grid'
import {
  TabsManager,
  TabHandler,
  TabContent,
  TabsBody,
  TabsHeader,
} from '../../components/Tabs';
import './tabs.scss';

function Tabs({ tabs, defaultTab }) {
  return (
    <GridContainer>
      <Grid variant="centered">
        <GridColumn columns="11">
          <Grid variant="centered">
            <TabsManager defaultTab={defaultTab}>
              <TabsHeader>
                {tabs &&
                  tabs.map((tab) => (
                    <TabHandler
                      isActive={tab.isActive}
                      tabId={tab.id}
                      key={tab.id}
                    >
                      {tab.title}
                    </TabHandler>
                  ))}
              </TabsHeader>
              <div className="tabs">
              <TabsBody >
                {tabs &&
                  tabs.map((tab) => (
                    <div className="tabs__content" key={tab.id}>
                      <TabContent
                      isActive={tab.isActive}
                      tabId={tab.id}
                    >
                      {tab.content}
                    </TabContent>
                    </div>
                  ))}
              </TabsBody>
              </div>
            </TabsManager>
          </Grid>
        </GridColumn>
      </Grid>
    </GridContainer>
  )
}

Tabs.propTypes = {
  tabs: PropTypes.array.isRequired,
  defaultTab: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
}
export default Tabs
