import { useState, createContext, useContext } from 'react'
import React from 'react';
import PropTypes from 'prop-types'
import Paper from '../Paper'
import { Grid } from '../Grid'
import { TitleAccentLine } from '../Title'
import "./tabs.scss";

const TabsContext = createContext('tabs');

function TabsManager({ children, defaultTab = '1' }) {
  const [tabsState, setTabState] = useState({
    currentTab: defaultTab,
  })
  const handleSetTabState = (tabId) => setTabState({ currentTab: tabId })

  return (
    <TabsContext.Provider
      value={{ currentTab: tabsState.currentTab, handleSetTabState }}
    >
      <div className="tabs-manager">{children}</div>
    </TabsContext.Provider>
  )
}

TabsManager.propTypes = {
  children: PropTypes.node,
  defaultTab: PropTypes.string,
}

function TabHandler({ children, tabId }) {
  const TabContext = useContext(TabsContext)
  return TabContext.currentTab === tabId ? (
    <Paper variant="tab" data-active="true" className="tab-handler__button tab-handler__button--active">
      <TitleAccentLine
        variant="title"
        className="tabs-handler__paper-title">
        {children}
      </TitleAccentLine>
    </Paper>
  ) : (
    <button
      className="tab-handler__button tab-handler__button--inactive"
      onClick={() => TabContext.handleSetTabState(tabId)}>
      <TitleAccentLine
        variant="title"
        className="tab-handler__title">
        {children}
      </TitleAccentLine>
    </button>
  )
}

TabHandler.propTypes = {
  children: PropTypes.node.isRequired,
  tabId: PropTypes.string.isRequired,
}

function TabContent({ children, tabId }) {
  const TabContext = useContext(TabsContext)
  return TabContext.currentTab === tabId ? <div>{children}</div> : null
}

TabContent.propTypes = {
  children: PropTypes.node.isRequired,
  tabId: PropTypes.string.isRequired,
}

function TabsHeader({ children }) {
  return (
    <div className="tabs-header">
      <Grid>{children}</Grid>
    </div>
  )
}

TabsHeader.propTypes = {
  children: PropTypes.node,
}

function TabsBody({ children }) {
  return <div className="tabs-body">{children}</div>
}

TabsBody.propTypes = {
  children: PropTypes.node,
}

export { TabsManager, TabsHeader, TabsBody, TabHandler, TabContent }
