import React from 'react'
import PropTypes from 'prop-types'
import { List, ListItem } from '../../components/List'
import PurpleArrow from '../../static/PurpleArrow.png'

function ListBlock({ items, variant = 'default', ...props }) {
  return (
    <List variant={variant}>
      {items &&
        items.map((item) => (
          <ListItem key={item} {...props}>
            <div className="list__block">
              {variant === 'byTwoColumns' && <img src={PurpleArrow} alt="purple arrow" className="list__block-arrow" />}{item}
            </div>
          </ListItem>
        ))}
    </List>
  )
}

ListBlock.propTypes = {
  items: PropTypes.array.isRequired,
  variant: PropTypes.string,
}

export default ListBlock
