import PropTypes from 'prop-types'
import React from 'react';
import cx from "classnames";
import "./list.scss";
// import PurpleArrow from '../../static/PurpleArrow.png'

function List({ children, variant = 'default', className }) {
  return (
    <ul className={cx(`list ${className ? className : ''}`, {
      [`list--${variant}`]: variant,
    })}>
      {children}
    </ul>
  )
}

List.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
  className: PropTypes.string,
}

function ListItem({ children, variant = 'default' }) {
  return (
    <li className={cx("list__item", {
      [`list__item--${variant}`]: variant,
    })}>
     {children}
    </li>
  )
}

ListItem.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
};

export { List, ListItem }
