import React from 'react'
import PropTypes from 'prop-types'
import { Styled, Box } from 'theme-ui'

function Paragraph({ children, variant, ...props }) {
  return (
    <Styled.p as="p" sx={{ variant: variant }} {...props}>
      {children}
    </Styled.p>
  )
}

Paragraph.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
}

function ParagraphCustom({ children, variant, ...props }) {
  return (
    <Box __themeKey="title" variant={variant} {...props}>
      {children}
    </Box>
  )
}

ParagraphCustom.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
}
export { Paragraph, ParagraphCustom }
