import PropTypes from 'prop-types'
import React from 'react';
import { Title } from '../Title';
import "./steps.scss";

function Steps({ children }) {
  return (
    <div className="steps">
      {children}
    </div>
  )
}

Steps.propTypes = {
  children: PropTypes.node.isRequired,
}

function Step({ children, label }) {
  return (
    <div className="steps__step">
      <div className="steps__step-label">
        {label}
      </div>
      {children}
    </div>
  )
}

Step.propTypes = {
  children: PropTypes.node.isRequired,
  label: PropTypes.string.isRequired,
}

function StepContent({ children }) {
  const TitleSlot = children.filter((child) => child.type === Title)
  const LeftChildren = children.filter((child) => child.type !== Title)
  return (
    <div className="step-content">
      {TitleSlot && (
        <div className="step-content__title">
          {TitleSlot}
        </div>
      )}
      {LeftChildren && (
        <div className="step-content__left">
          {LeftChildren}
        </div>
      )}
    </div>
  )
}

StepContent.propTypes = {
  children: PropTypes.node.isRequired,
}

export { Steps, Step, StepContent }
