import PropTypes from 'prop-types'
import React from 'react';
import Cta from '../../components/Cta'
import {Image} from '../../components/Image'
import PlusImage from '../../static/plus.png'
import "./cta-section.block.scss";

function CtaSectionBlock({heading, cta, variant}) {
  return (
    <div className={`cta-section-block cta-section-block--${variant}`}>
      <div className="cta-section-block__left">
        {heading}
      </div>
      <div className="cta-section-block__right">
        <Cta to={cta.to} variant={cta.variant}>
          {cta.text}
          <Image variant="cta" src={PlusImage} alt="plus icon"/>
        </Cta>
      </div>
    </div>
  )
}

CtaSectionBlock.propTypes = {
  cta: PropTypes.shape({
    text: PropTypes.string,
    to: PropTypes.string.isRequired,
    variant: PropTypes.string,
  }).isRequired,
  heading: PropTypes.node.isRequired,
  variant: PropTypes.string,
}
CtaSectionBlock.defaultProps = {
  variant: 'plain',
}
export default CtaSectionBlock
